import Vue from "vue";
import VueRouter from "vue-router";
import ImpressumPage from "@/pages/ImpressumPage.vue";
import HomePage from "@/pages/HomePage.vue";

Vue.use(VueRouter);
export default new VueRouter({
    mode: "history",
    routes: [
        {
            path: '/',
            name: 'Home',
            component: HomePage
        },
        {
            path: '/impressum',
            name: 'Impressum',
            component: ImpressumPage
        }
    ],
});