<template>
  <div id="app">
    <HomePage />
  </div>
</template>

<script>
import HomePage from "@/pages/HomePage.vue";
import Bowser from "bowser";
import {mapGetters} from "vuex";

export default {
  name: 'App',
  components: {HomePage},
  mounted() {
    this.updateIsMobile()
    window.addEventListener('resize', this.updateIsMobile, true);
  },
  destroyed() {
    window.removeEventListener('resize', this.updateIsMobile, true)
  },
  computed: {
    ...mapGetters(['isMobile'])
  },
  methods: {
    updateIsMobile() {
      const parsedResult = Bowser.getParser(window.navigator.userAgent).getResult();

      const isMobile = ((parsedResult.platform.type === 'mobile' ||
                  parsedResult.platform.type === 'tablet') &&
              window.innerWidth < 993) ||
          window.innerWidth < 993;

      this.$store.commit("setIsMobile", isMobile);
    }
  }
}
</script>

<style>
body {
  border: 0;
  padding: 0;
  margin: 0;
}

#app {
  min-width: 100vw;
  min-height: 100vh;
  background: #d8cfbd;;
}
</style>
