import Vue from "vue";
import Vuex from "vuex";
Vue.use(Vuex);

export default new Vuex.Store({
    state: {
        isMobile: false,
    },
    getters: {
        isMobile: (state) => {
            return state.isMobile;
        },
    },
    mutations: {
        setIsMobile(state, isMobile) {
            state.isMobile = isMobile;
        },
    },
});